import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsLookupDebitCardsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-debit-cards/ps-lookup-debit-cards.component.interfaces';
import { CardsService } from 'src/app/commonBussinessSRC/psServices/Cards/cards.service';
import { IOmniCardsRequest, PrepaidCardRequestVO } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsContainerPanel, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { OmniBasePage } from './../omni-base/omni-base.page';

@Component({
  selector: 'prepaid-card-request',
  templateUrl: './prepaid-card-request.page.html',
  styleUrls: ['./prepaid-card-request.page.scss'],
})
export class PrepaidCardRequestPage extends OmniBasePage implements OnInit {

  formGroup: FormGroup = new FormGroup({});
  maxCardsAllowed: number
  prepaidCardRequestVO: PrepaidCardRequestVO = {};
  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'prepaid_card_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: [
      'prepaid_card_req_step1',
    ],
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.creditCardRequest,
      group: this.formGroup,
      preCallFunction: {
        func(response) {
          return new Promise<any>((resolve, reject) => {
            resolve(this.executionClass.updateScreenVo(response));
          });
        },
        executionClass: this,
        params: [this]
      }
    },
    requestObject: this.prepaidCardRequestVO,
  };

  panelOptions1Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'prepaid_card_main_details_key',
    iconName: 'new-card-request',
    expanded: true,
  };

  debitCardsLookupOptiops: IOptionsPsLookupDebitCardsExposed = {
    labelKey: 'reference_card_key',
    placeHolder: 'select_reference_card_key',
    group: this.formGroup,
    fcName: 'referenceCard',
    requestObject: this.prepaidCardRequestVO,
  };
  constructor(public logger: LoggerService,
    public navService: PsNavigatorService,
    public commonService: PsCommonService,
    private crdsSrvce: CardsService,
    public omniPull?: OmniPullService
  ) {
    super();
  }

  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.checkMaxCardsNo();
    this.commonProv.copyObject(this.prepaidCardRequestVO, this.navService.getAllParams() ? this.navService.getAllParams().ScreenVO ? this.navService.getAllParams().ScreenVO : this.navService.getAllParams() : {})


  }
  //check on prepaid cards limit from admin if exceeded alert and redirect to landing page
  checkMaxCardsNo() {
    this.omniPull.getParamValOf("MaximumPrepaidCardsAllowed").then(res => {
      this.maxCardsAllowed = res.MaximumPrepaidCardsAllowed
    }).catch(err => this.logger.log(err));
    const paramData: IOmniCardsRequest = {
      language: 'EN',
      cardType: 'Prepaid'
    };
    this.crdsSrvce.returnCardList(paramData).then((result) => {
      if (result && result.gridModel.length > 0) {
        if (result.gridModel.length > this.maxCardsAllowed || result.gridModel.length == this.maxCardsAllowed) {
          CommonUtils.presentFailureAlert('maximum_prepaid_cards_allowed_reached', { autoHide: false }).then(() => {
            PsCommonSettings.oper_ID = ConstantCommon.LANDING_OPER_ID;
            this.navService.navigateToMain();
            this.commonService.dismissLoading();
          });
        }
      }
    }).catch(error => {
      this.logger.error('Error: While fetching prepaid cards list :', error);
    });

  }

  updateScreenVo(resp) {
    this.prepaidCardRequestVO.submitType = '-1'; // Added by: MFawzy - tp: #1542492 - as requested per Doris to get the correct mappingId with the submit service
  }
}
